import { VotingVault } from "src/generated";
import { AddressName } from "src/logic/addressLists";
import { getAddresses } from "src/logic/utils/getAddresses";
import { CouncilResolverContext } from "src/resolvers/context";

interface VotingVaultModel {
  getByName: (options: {
    name: AddressName;
    context: CouncilResolverContext;
  }) => VotingVault;

  getByNames: (options: {
    names: AddressName[];
    context: CouncilResolverContext;
  }) => VotingVault[];
}

export const VotingVaultModel: VotingVaultModel = {
  getByName({ name, context }) {
    // prefer plural to getAddresses once
    const vaults = this.getByNames({ names: [name], context });
    return vaults[0];
  },
  getByNames({ names, context: { chainId } }): VotingVault[] {
    const addresses = getAddresses(chainId);
    return names.map((name) => ({
      address: addresses[name],
      name,
    }));
  },
};
