import { AddressName } from "src/logic/addressLists";
import { CouncilResolverContext } from "src/resolvers/context";

export function getDataSourceByName(
  name: AddressName,
  dataSources: CouncilResolverContext["dataSources"],
) {
  switch (name) {
    case "gscCoreVoting":
      return dataSources.gscVoting;
    case "gscVault":
      return dataSources.gscVault;
    case "lockingVault":
      return dataSources.lockingVault;
    case "vestingVault":
      return dataSources.vestingVault;
    case "coreVoting":
    default:
      return dataSources.coreVoting;
  }
}
