import { AddressName } from "src/logic/addressLists";
import { getAddresses } from "./getAddresses";

export function getAddressName(
  address: string,
  chainId: number,
): AddressName | undefined {
  const addresses = getAddresses(chainId);
  for (const [name, nameAddress] of Object.entries(addresses)) {
    if (address === nameAddress) {
      return name as AddressName;
    }
  }
}
