import { VotingContract, VotingVault } from "src/generated";
import { AddressName } from "src/logic/addressLists";
import { getAddresses } from "src/logic/utils/getAddresses";
import { getAddressName } from "src/logic/utils/getAddressName";
import { CouncilResolverContext } from "src/resolvers/context";
import { VotingVaultModel } from "./VotingVault";

interface VotingContractModel {
  getByAddress: (options: {
    address: string;
    context: CouncilResolverContext;
  }) => VotingContract | null;

  getByName: (options: {
    name: AddressName;
    context: CouncilResolverContext;
  }) => VotingContract;
}

export const VotingContractModel: VotingContractModel = {
  getByAddress({ address, context }) {
    const name = getAddressName(address, context.chainId);
    return name ? this.getByName({ name, context }) : null;
  },
  getByName({ name, context }) {
    const addresses = getAddresses(context.chainId);
    let votingVaults: VotingVault[];
    switch (name) {
      case "coreVoting":
        votingVaults = [
          VotingVaultModel.getByName({ name: "lockingVault", context }),
          VotingVaultModel.getByName({ name: "vestingVault", context }),
        ];
        break;
      case "gscCoreVoting":
        votingVaults = [
          VotingVaultModel.getByName({ name: "gscVault", context }),
        ];
        break;
      default:
        votingVaults = [];
    }
    return {
      address: addresses[name],
      name,
      votingVaults,
    };
  },
};
